import {
  AppBar,
  Toolbar,
  Typography,
  styled,
  Box,
  alpha,
  InputBase,
  Tabs,
  Tab,
} from '@mui/material';
import type { TabProps } from '@mui/material';
import { Search as SearchIcon } from '@mui/icons-material';
import React from 'react';

const Search = styled('div')(({ theme }) => ({
  position: 'relative',
  borderRadius: theme.shape.borderRadius,
  backgroundColor: alpha(theme.palette.common.white, 0.15),
  '&:hover': {
    backgroundColor: alpha(theme.palette.common.white, 0.25),
  },
  marginLeft: theme.spacing(1),
  width: 'auto',
}));

const SearchIconWrapper = styled('div')(({ theme }) => ({
  padding: theme.spacing(0, 2),
  height: '100%',
  position: 'absolute',
  pointerEvents: 'none',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
}));

const StyledInputBase = styled(InputBase)(({ theme }) => ({
  color: 'inherit',
  '& .MuiInputBase-input': {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)})`,
    transition: theme.transitions.create('width'),
    width: '12ch',
    '&:focus': {
      width: '20ch',
    },
  },
}));

const StyledTab = styled((props: TabProps) => <Tab disableRipple {...props} />)(
  ({ theme }) => ({
    color: alpha(theme.palette.common.black, 0.25),
    '&.Mui-selected': {
      color: theme.palette.common.black,
    },
    '&.Mui-focusVisible': {
      backgroundColor: alpha(theme.palette.common.black, 0.65),
    },
  })
);

const topbar: React.FC = () => {
  return (
    <AppBar position="static" color="inherit" elevation={0}>
      <Toolbar>
        <Typography
          variant="h6"
          noWrap
          component="a"
          sx={{
            mr: 2,
            display: 'flex',
            fontFamily: 'monospace',
            fontWeight: 700,
            letterSpacing: '.3rem',
          }}
        >
          Blog
        </Typography>
        <Search>
          <SearchIconWrapper>
            <SearchIcon />
          </SearchIconWrapper>
          <StyledInputBase
            placeholder="Search…"
            inputProps={{ 'aria-label': 'search' }}
          />
        </Search>
        <Box sx={{ flexGrow: 1, display: 'flex', justifyContent: 'right' }}>
          <Tabs value={0} centered>
            <StyledTab label="前端" />
            <StyledTab label="网络" disabled />
            <StyledTab label="编程" disabled />
            <StyledTab label="React" disabled />
            <StyledTab label="Vue" disabled />
          </Tabs>
        </Box>
      </Toolbar>
    </AppBar>
  );
};

export default topbar;
